import React from "react";
import "../../App.css";
import { NavLink } from "react-router-dom";
const SideBar = ({ mendues }) => {
  return (
    <div className="sidePanel">
      {mendues.map((menu, i) => {
        return (
          <ul>
            <li>
              <NavLink to={{ pathname: menu.routeLink }} style={{ textDecoration: 'none' ,color:'blue'}}>
                {menu.menu}
              </NavLink>
            </li>
          </ul>
        );
      })}
    </div>
  );
};

export default SideBar;
