import React, { Component } from "react";

import "../../App.css";
import { CommonButton } from "../../components";
import { Button } from 'semantic-ui-react'
class AdministrtorSetup extends Component {
  constructor(props) {
    super(props);

    this.state = {
      list1: [
        {
            id : '123',
            name: "megh"
        },
        {
          id : '124',
          name: "akshay"
        },
        {
          id : '125',
          name: "Akash"
        }
        
      ],
      list2: [],
    };
  }

  addHandler = (name, itemId) => {
   
    
    var joined;
    joined = this.state.list2.concat({name:name,id:itemId});
    this.setState({
      list2: joined,
    });
  };
  removeHandler = (name, itemId) => {
    var removed = this.state.list2.filter((ele) => {
      return ele.id == itemId;
    });
    console.log("remove ele-->", removed);
    var carIndex = this.state.list2.indexOf(removed);
    this.state.list2.splice(carIndex, 1);
    if (removed != name) {
      removed = this.state.list1.concat(removed);
    } else {
      removed = this.state.list1;
    }
    this.setState({
      list1: removed,
    });
  };

  render() {
    const { list1, list2 } = this.state;
    return (
      <div
        style={{
          padding: 10,
          display: "flex",
          flexDirection: "column",
          flex: 1,
        }}
      >
        <p style={{ fontWeight: "bold", fontSize: 20, alignSelf: "center" }}>
          Administrator Setup
        </p>

        <div className="buttonConatiner">
          <div className="buttonBox1">
            <CommonButton title="Save" overrideStyle={"buttonBox1"} />
          
          </div>

          <div className="buttonBox2">
            <select id="dropdown">
              <option value="N/A">N/A</option>
              <option value="1">1</option>
              <option value="2">2</option>
              <option value="3">3</option>
              <option value="4">4</option>
            </select>
            <CommonButton title="Clear" overrideStyle={"buttonBox2"} />
          </div>
        </div>
        <div
          style={{
            display: "flex",

            alignSelf: "center",
            marginTop: 20,
          }}
        >
          <div
            style={{
              width: 250,
              height: 250,
              overflow: "auto",
              paddingLeft: 10,
              backgroundColor: "#fff",
            }}
            className="listBox"
          >
            {list1.map((item, i) => {
              return (
                <ul>
                  <li onClick={() => this.addHandler(item.name,item.id)}>{item.name}</li>
                </ul>
              );
            })}
          </div>
          <div
            style={{
              display: "flex",
              padding: 10,
              flexDirection: "column",
              alignItems: "center",
              marginTop: 70,
            }}
          >
            <CommonButton title={">>"} />
            <CommonButton title={"<<"} />
          </div>
          <div
            style={{
              width: 250,
              height: 250,
              paddingLeft: 10,
              backgroundColor: "#fff",
              overflow: "auto",
            }}
            className="listBox"
          >
            {list2.map((item, i) => {
              return (
                <ul>
                  <li onClick={() => this.removeHandler(item.name.item.id)}>{item.name}</li>
                </ul>
              );
            })}
          </div>
        </div>
        
      </div>
    );
  }
}

export default AdministrtorSetup;
