import React, { Component } from "react";
import "../../App.css";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div
        style={{
          padding: 10,
          display: "flex",
          flexDirection: "column",
          flex: 1,
          alignItems: "center",
        }}
      >
        <p style={{ fontWeight: "bold", fontSize: 20 }}>
          Welcome to Calculate Pi
        </p>

        <div
          style={{
            display: "flex",
            flexDirection: "row",

            padding: 10,
            backgroundColor: "red",
        
          }}
        >
          <div style={{ display: "inline", width: 25 }}>
            <input type="radio" id="male" name="gender" value="male" />
          </div>

          <div style={{ display: "flex", width: 200 }}>
            <label for="male">Simulation</label>
          </div>
        </div>

        <div
          style={{
            display: "flex",
            flexDirection: "row",
            alignSelf: "center",
          }}
        >
          <div style={{ display: "inline", width: 25 }}>
            <input type="radio" id="male" name="gender" value="female" />
          </div>

          <div style={{ display: "inline", width: 200 }}>
            <label for="male">Production</label>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
